import { createActionHandlers } from '@ali/4ever-cangjie';
const ACTION_EXIT_PREVIEW = Symbol('ACTION_EXIT_PREVIEW');
const ACTION_ENTER_PREVIEW = Symbol('ACTION_ENTER_PREVIEW');

export default {
  EXIT: ACTION_EXIT_PREVIEW,
  ENTER: ACTION_ENTER_PREVIEW,
};

export const [exitPreview, isExitPreview] = createActionHandlers(ACTION_EXIT_PREVIEW);
export const [enterPreview, isEnterPreview] = createActionHandlers(ACTION_ENTER_PREVIEW);
